import React, { useEffect, useState } from "react";
import { firebaseApp, database } from "./firebase";
import { getDatabase, ref, onValue } from "firebase/database";
import Header from "./components/Header";
import Section from "./components/Section";
import Footer from "./components/Footer";

const App = () => {
  const [weight, setWeight] = useState(0);
  const [height, setHeight] = useState(0);
  const [age, setAge] = useState(0);
  const [gender, setGender] = useState("");
  const [bmi, setBMI] = useState("");
  const [message, setMessage] = useState("");
  const [params, setParams] = useState();

  const fetchBmi = () => {
    const dbRef = getDatabase();
    const stats = ref(dbRef, `${gender}/${age}/`);
    onValue(stats, (snapshot) => {
      const data = snapshot.val();
      setParams(data);
    });
  };

  useEffect(() => {
    if (params) {
      const { veryLow, low, normal, high, veryHigh } = params;

      if (weight === 0 || height === 0 || age === 0) {
        alert("  ");
      } else {
        let bmi = weight / ((height / 100) * (height / 100));
        setBMI(bmi.toFixed(1));

        if (bmi < veryLow) {
          setMessage("    ");
        }

        if (bmi >= veryLow && bmi < low) {
          setMessage("   ");
        }

        if (bmi >= low && bmi < high) {
          setMessage("   ");
        }

        if (bmi >= high && bmi < veryHigh) {
          setMessage("   ");
        }

        if (bmi > veryHigh) {
          setMessage("  ");
        }
      }
    }
  }, [age, height, weight, params]);

  console.log("params", params);

  return (
    <div className="App">
      <Header />
      <Section />
      <div className="bmi-container">
        <h2 className="center"> </h2>
        {/* <form onSubmit={calcBmi}> */}
        <div>
          <label></label>
          <select
            value={gender}
            onChange={(event) => setGender(event.target.value)}
          >
            <option selected value>
               
            </option>
            <option>F</option>
            <option>M</option>
          </select>
        </div>
        <div>
          <label></label>
          <input value={age} onChange={(event) => setAge(event.target.value)} />
        </div>
        <div>
          <label> ( )</label>
          <input
            value={weight}
            onChange={(event) => setWeight(event.target.value)}
          />
        </div>
        <div>
          <label> ( )</label>
          <input
            value={height}
            onChange={(event) => setHeight(event.target.value)}
          />
        </div>
        <div>
          <button className="btn" onClick={fetchBmi}>
            
          </button>
        </div>
        {/* </form> */}

        <div className="center">
          <h3> : {bmi}</h3>
          <p>{message}</p>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default App;
